      <div class="search_container">
        <el-form :inline="true" :model="params" class="demo-form-inline">
          <el-form-item label="库存组织" size="mini">
            <el-select clearable v-model="params.company_id" placeholder="请选择" filterable @focus="companyListInvok" @change="companyChange">
              <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
                <span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}</span>
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="业务日期" size="mini">
            <el-date-picker v-model="timeValue" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="单据号" size="mini">
            <el-input v-model="params.stock_taking_sn" placeholder="请输入单据号" @keyup.enter.native="onSearch"></el-input>
          </el-form-item>

          <el-form-item label="仓库名称" size="mini">
            <el-select clearable v-model="params.warehouse_id" placeholder="请选择" @focus="warehouseInvok" @change="warehouseChange">
              <el-option :label="item.warehouse_name" :value="item.warehouse_id" :key="item.warehouse_id" v-for="item in warehouseList"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="库管员" size="mini">
            <el-select clearable v-model="params.keeper_id" placeholder="请选择" filterable @focus="EmployeeListInvok" @change="employeeChange">
              <el-option :label="item.employee_name" :value="item.employee_id" :key="item.employee_id" v-for="item in employeeList"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="盘盈入库单号" size="mini">
            <el-input v-model="params.instorage_sn" placeholder="请输入盘盈入库单号" @keyup.enter.native="onSearch"></el-input>
          </el-form-item>
          <el-form-item label="盘亏出库单号" size="mini">
            <el-input v-model="params.outstock_sn" placeholder="请输入盘亏出库单号" @keyup.enter.native="onSearch"></el-input>
          </el-form-item>
          <el-form-item label="审核状态" size="mini">
            <el-select clearable v-model="params.auditor_status" placeholder="请选择">
              <el-option label="全部" :value="0">全部</el-option>
              <el-option label="未审核" :value="1">未审核</el-option>
              <el-option label="已审核" :value="2">已审核</el-option>
              <el-option label="作废" :value="3">作废</el-option>
            </el-select>
          </el-form-item>
          <el-form-item size="mini" class="search-style search">
            <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="add_delet">
        <el-button icon="el-icon-circle-plus-outline" type="primary" @click="AddF" class="addBtn" size="mini" v-if="authority.b_add==1">新增</el-button>
        <el-button plain icon="el-icon-delete" @click="delF" size="mini" v-if="authority.b_del==1">删除</el-button>
        <el-button icon="iconfont icon-pass" type="primary" @click="auditorOrInvalid(1)" size="mini" v-if="authority.b_pass==1">审核</el-button>
        <el-button plain icon="iconfont icon-zuofei" @click="auditorOrInvalid(2)" size="mini" v-if="authority.b_invalid==1">作废</el-button>
      </div>
      <div class="table_container">
        <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" max-height="600" style="width: 100%" @selection-change="handleSelectionChange" v-loading="loading1">
          <el-table-column :show-overflow-tooltip="true" type="selection" fixed="left" align="center" width="44">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" type="index" align="center" width="44" label='序号'>
          </el-table-column>
          <el-table-column label="操作" fixed="right" width="100">
            <template slot-scope="scope">
              <el-button @click="seeDetail(scope, scope.row)" type="text" size="small" v-if="authority.b_info==1">
                查看
              </el-button>
              <el-button @click="AddF(scope, scope.row)" type="text" size="small" v-if="scope.row.auditor_status==1 && authority.b_edit==1">
                修改
              </el-button>
              <el-button disabled type="text" size="small" v-else>
                修改
              </el-button>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="company_name" min-width="180" label="库存组织">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="stock_taking_sn" min-width="170" label="单据号">
            <template slot-scope="scope">
              <el-button @click="seeDetail(scope, scope.row)" type="text" size="small">
                {{scope.row.stock_taking_sn }}
              </el-button>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="business_time" min-width="100" label="业务日期">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="warehouse_name" min-width="150" label="仓库名称">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="total_stocks" min-width="100" label="账面总数量" align="right">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="total_real_stocks" min-width="100" label="实盘总数量" align="right">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="total_stocks_num" min-width="100" label="盘盈/亏总数量" align="right">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="keeper_name" min-width="100" label="库管员">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="remark" min-width="100" label="备注">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="auditor_status_name" min-width="100" label="审核状态">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="add_user_name" min-width="100" label="创建人">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="add_time" min-width="160" label="单据时间">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="auditor_name" min-width="100" label="审核人">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="auditor_time" min-width="160" label="审核时间">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="update_user_name" min-width="100" label="修改人">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="update_time" min-width="160" label="修改时间">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="instorage_sn" min-width="160" label="盘盈入库单号">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="outstock_sn" min-width="160" label="盘亏出库单号">
          </el-table-column>
        </el-table>
      </div>
      <div class="page_container">
        <div class="pageNumber">每页显示</div>
        <el-pagination class="erp-pagination" @size-change="handleSizeChange" :current-page.sync="goPage" :page-size="pageSize1" @current-change="handleCurrentChange" :page-sizes="per_page" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
        </el-pagination>
      </div>
    </div>
  </div>
</template>
<script>
import { mapActions, mapGetters } from "vuex";
import {
  getData,
  repertory,
  opthionList,
} from "common/utils/public_function/commonUtils";
export default {
  name: "instorageTakestockTakestockIndex",
  data() {
    return {
      timeValue2: [],
      loading: false,
      dialogFormVisible: false,
    };
  },
  computed: {
    ...mapGetters(["takestockIndexGet"]),
  },
  methods: {
    ...mapActions([
      "takestockAddACT",
      "takestockSeeACT",
      "takestockModifyACT",
      "takestockIndexACT",
    ]),
    companyChange(val) {
      if (val) {
        this.params.company_name = this.companyList.filter(
          (obj) => obj.company_id === val
        )[0].company_name;
      }
      this.$set(this.params, "warehouse_id", "");
      this.$set(this.params, "keeper_id", "");
    },
    warehouseChange(val) {
      if (val) {
        this.params.warehouse_name = this.warehouseList.filter(
          (obj) => obj.warehouse_id === val
        )[0].warehouse_name;
      }
    },
    employeeChange(val) {
      if (val) {
        this.params.keeper_name = this.employeeList.filter(
          (obj) => obj.employee_id === val
        )[0].employee_name;
      }
    },
    auditorOrInvalid(status) {
      let vm = this;
      let api, txt, esData;
      if (status == 1) {
        api = "auditorPassTakeStock";
        txt = "审核";
        esData = Object.assign({}, this.searchParams.params);
      } else {
        api = "auditorInvalidTakeStock";
        txt = "作废";
        esData = Object.assign({}, this.searchParams.params, { type: "2" });
      }

      if (this.multipleSelection.length < 1) {
        this.$message({
          message: `请选择需要${txt}的信息`,
          type: "warning",
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].stock_taking_id);
      }
      var stock_taking_id = addr.toString();
      var funcan = {
        noConfirm: true,
        examineID: "stock_taking_id",
        eachData: {
          stock_taking_id: stock_taking_id,
        },
        examineApi: api,
        list: {
          UrlApi: "getTakeStockList",
          data: this.TableData,
          esData: esData,
        },
      };
      if (status == 1) {
        // 校验安全库存金额
        this.$axMethod
          .safetyStockCheck({
            bill_logogram: "pkck",
            order_type: 2,
            bill_id: stock_taking_id,
          })
          .then((data) => {
            repertory(
              this,
              {
                goods_info: stock_taking_id,
                status: 2,
                type: 6,
              },
              function () {
                vm.$common.examineFun(funcan);
              }
            );
          });
      } else {
        this.$common.examineFun(funcan);
      }
    },
    seeDetail(scope, row) {
      this.$router.push({
        path: "/inventory/takestock/takestockSee",
      });
      this.takestockSeeACT({
        stock_taking_id: row.stock_taking_id,
        type: this.type,
        goPage: this.goPage,
        params: this.params,
        pageSize: this.pageSize,
        after_auditor_print_status: row.after_auditor_print_status,
      });
    },
    AddF(scope, row) {
      this.$router.push({
        path: "/inventory/takestock/takestockAdd",
      });
      this.takestockAddACT({
        goPage: this.goPage,
        params: this.params,
        pageSize: this.pageSize,
        stock_taking_id: row ? row.stock_taking_id : "",
      });
    },
    onReset(formName) {
      this.$set(this, "params", { auditor_status: 0, type: this.type });
      this.timeValue = [];
      this.timeValue2 = null;
      this.$set(this, "searchParams", {
        params: this.params,
        TableData: this.TableData, //列表数据
        API: "getTakeStockList", //接口名
        vm: this, //传递的this
      });
      getData(this.searchParams);
    },
    onSearch() {
      if (this.timeValue && this.timeValue.length == 2) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }
      if (this.timeValue2 && this.timeValue2.length == 2) {
        this.params.aud_start_time = this.timeValue2[0];
        this.params.aud_end_time = this.timeValue2[1];
      } else {
        this.params.aud_start_time = this.params.aud_end_time = "";
      }
      Object.assign(this.searchParams.params, this.params, {
        page: 1,
      });
      this.takestockIndexACT({
        params: this.params,
        goPage: this.goPage,
        pageSize: this.pageSize,
      });
      getData(this.searchParams);
    },
    //删除
    delF() {
      var deledata = {
        deleID: "stock_taking_id",
        deleSelect: this.multipleSelection,
        deleApi: "delTakeStock",
        list: {
          UrlApi: "getTakeStockList",
          data: this.TableData,
          esData: this.searchParams.params,
        },
      };
      this.$common.deletes(deledata, "删除", this);
    },
  },
  mounted() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    this.$set(this, "params", { auditor_status: 0 });
    if (this.takestockIndexGet && this.takestockIndexGet.params) {
      if (this.takestockIndexGet.params.start_time) {
        const { start_time, end_time } = this.takestockIndexGet.params;
        this.timeValue = this.timeValue.concat([start_time, end_time]);
      }
      Object.assign(this.takestockIndexGet.params, {
        employee_id: this.takestockIndexGet.params.keeper_id,
        employee_name: this.takestockIndexGet.params.keeper_name,
      });
      this.$set(this, "params", this.takestockIndexGet.params);
      opthionList(this, this.takestockIndexGet.params);
    }
    if (this.takestockIndexGet.goPage) {
      this.params.page = this.takestockIndexGet.goPage;
      this.params.per_page = this.takestockIndexGet.pageSize;
    }
    this.$set(this, "searchParams", {
      params: this.params, //1默认带的参数
      TableData: this.TableData, //列表数据
      API: "getTakeStockList", //接口名
      vm: this, //传递的this
    });
    getData(this.searchParams); //获取库存订单列表
  },
};
</script>
